"use strict";
import React from "react";

export class StepOne extends React.Component {
  constructor() {
    super();
    this.state = {
      studentName: "",
      email: "",
      mobile: "",
      homephone: "",
      address1: "",
      address2: "",
      postcode: ""
    };

    this.handleStudentNameChanged = this.handleStudentNameChanged.bind(this);
    this.handleEmailChanged = this.handleEmailChanged.bind(this);
    this.handleMobileChanged = this.handleMobileChanged.bind(this);
    this.handleHomePhoneChanged = this.handleHomePhoneChanged.bind(this);
    this.handleAddress1Changed = this.handleAddress1Changed.bind(this);
    this.handleAddress2Changed = this.handleAddress2Changed.bind(this);
    this.handlePostCodeChanged = this.handlePostCodeChanged.bind(this);
  }

  handleStudentNameChanged(event) {
    this.setState({ studentName: event.target.value });
  }

  handleEmailChanged(event) {
    this.setState({ email: event.target.value });
  }

  handleMobileChanged(event) {
    this.setState({ mobile: event.target.value });
  }

  handleHomePhoneChanged(event) {
    this.setState({ homephone: event.target.value });
  }

  handleAddress1Changed(event) {
    this.setState({ address1: event.target.value });
  }

  handleAddress2Changed(event) {
    this.setState({ address2: event.target.value });
  }

  handlePostCodeChanged(event) {
    this.setState({ postcode: event.target.value });
  }

  render() {
    return (
      <div className="container section">
        <div className="row">
          <div className="col-md-8 mx-auto">
            <div className="form-group">
              <input
                type="text"
                id="name"
                className="form-control"
                required
                onChange={this.handleStudentNameChanged}
                value={this.state.studentName}
                autoFocus
              />
              <label className="form-control-placeholder" htmlFor="name">
                Student Name
              </label>
            </div>
            <div className="form-group">
              <input
                type="text"
                id="email"
                className="form-control"
                required
                onChange={this.handleEmailChanged}
                value={this.state.email}
                autoFocus
              />
              <label className="form-control-placeholder" htmlFor="email">
                Email
              </label>
            </div>
            <div className="form-group">
              <input
                type="number"
                id="mobile"
                className="form-control"
                required
                onChange={this.handleMobileChanged}
                value={this.state.mobile}
                autoFocus
              />
              <label className="form-control-placeholder" htmlFor="mobile">
                Mobile
              </label>
            </div>
            <div className="form-group">
              <input
                type="number"
                id="phone"
                className="form-control"
                required
                onChange={this.handleHomePhoneChanged}
                value={this.state.homephone}
                autoFocus
              />
              <label className="form-control-placeholder" htmlFor="phone">
                Home Phone
              </label>
            </div>

            <div className="form-group">
              <input
                type="text"
                id="address1"
                className="form-control"
                required
                onChange={this.handleAddress1Changed}
                value={this.state.address1}
                autoFocus
              />
              <label className="form-control-placeholder" htmlFor="address1">
                Address 1
              </label>
            </div>

            <div className="form-group">
              <input
                type="text"
                id="address2"
                className="form-control"
                required
                onChange={this.handleAddress2Changed}
                value={this.state.address2}
                autoFocus
              />
              <label className="form-control-placeholder" htmlFor="address2">
                Address 2
              </label>
            </div>

            <div className="form-group">
              <input
                type="number"
                id="postcode"
                className="form-control"
                required
                onChange={this.handlePostCodeChanged}
                value={this.state.postcode}
                autoFocus
              />
              <label className="form-control-placeholder" htmlFor="postcode">
                Postal Code
              </label>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
